import Axios from 'axios';
import qs from 'qs';
import router from '../router/index'
import { Message } from "element-ui";

const axios = Axios.create({
	timeout: 80000,
	withCredentials: true,
    transformRequest: function(data) {
        return qs.stringify(data);
    }
});
axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';
axios.defaults.headers.common['csrfToken'] = '9EA6EC4DCBE5658676919DD8E4686095';

//POST()
axios.interceptors.request.use(
  config => {
    // 
    if (config.method === "post") {
      // 
      // config.data = qs.stringify(config.data)
      // ,json , qs 
    }

    // token , token
    // , cookie ,, localstorage 
    if (localStorage.token) {
      config.headers.Authorization = localStorage.token;
    }
    return config;
  },
  error => {
    // error ,
    Message({
      //  ,toast
      showClose: true,
      message: error,
      type: "error"
    });
    return Promise.reject(error.data);
  }
);

//()
axios.interceptors.response.use(
  res => {
      if(res.data.code == '-2'){
        sessionStorage.removeItem('islogin');
        router.replace('/');
      }
    return res;

  },function(error){
  	if (error.response && error.response.status && 403 === error.response.status) {
	    if(error.response.headers.redirect && error.response.headers.redirect === 'REDIRECT'){
	   		window.location = error.response.headers.contentpath
	    }
    } else {
    	Message({
	      //  ,toast
	      showClose: true,
	      message: '',
	      type: "error"
	    });
        return Promise.reject(error);
    }
  }
)

// axiosplugin , Vue.use(xxxx)
export default axios;


