<template>
  <div class='addmanagerLine-containter'>
    <div class = 'addmanagerLine'>
      <div class='inputBars'>
        <span style="color:red">*</span><span>:</span>
         <span style="margin-right:12px"></span>
        <el-input  class="managerAdd-input" v-model="nameInput" placeholder="" maxlength="20"></el-input>
      </div>
      <el-form :model="numberValidateForm" ref="numberValidateForm" label-width="100px" >
        <el-form-item
    label=":"
    prop="phone"
    :rules="[
      { required: true, message: ''},
      { type: 'number', message: ''}
    ]"
  >
    <el-input type="age" v-model.number="numberValidateForm.phone" maxlength="11"></el-input>
  </el-form-item>
  </el-form>
      <div class='inputBar'>
         <span>:</span>
          <span style="margin-right:12px"></span>
          <el-input  class="managerAdd-input" v-model="nicknameInput" placeholder="" maxlength="20"></el-input>
      </div>
    </div>
    <div class = 'addmanagerLine'>
      <div class='inputBars'>
        <span>:</span>
         <span style="margin-right:12px"></span>
         <el-select v-model="sexvalue" placeholder="">
          <el-option
            v-for="item in sexoptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
         </el-select>
      </div>
      <div class='inputBars'>
        <span>:</span>
         <span style="margin-right:12px"></span>
        <el-input  class="managerAdd-input" v-model="ageInput" placeholder="" maxlength="3"></el-input>
      </div>
      <div class='inputBars'>
        <span>:</span>
         <span style="margin-right:12px"></span>
         <el-select v-model="eduvalue" placeholder="">
          <el-option
            v-for="item in eduoptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
         </el-select>
      </div>
   
    </div>
    <div class="addmanagerLine">
         <div class='inputBars'>
        <span>:</span>
         <span style="margin-right:12px"></span>
         <el-select v-model="marvalue" placeholder="">
          <el-option
            v-for="item in maroptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
         </el-select>
      </div>
      <div class='inputBar'>
        <span>:</span>
         <span style="margin-right:12px"></span>
        <el-input  class="managerAdd-input" v-model="jobInput" placeholder="" maxlength="11"></el-input>
      </div>
      <div class='inputBar'>
        <span>:</span>
         <span style="margin-right:12px"></span>
        <el-input  class="managerAdd-input" v-model="addressInput" placeholder="" maxlength=""></el-input>
      </div>
    </div>
    <div class="addmanagerLine">
      <div class='inputBars'>
        <span>:</span>
         <span style="margin-right:12px"></span>
        <el-input  class="managerAdd-input" v-model="currentInput" placeholder="" maxlength=""></el-input>
      </div>
      <div class='inputBars'>
        <span>:</span>
         <span style="margin-right:12px"></span>
        <el-input  class="managerAdd-input" v-model="truenameInput" placeholder="" maxlength=""></el-input>
      </div>
        <div class='inputBars'>
        <span>:</span>
         <span style="margin-right:12px"></span>
        <el-input  class="managerAdd-input" v-model="idcardInput" placeholder="" maxlength="18"></el-input>
      </div>
    </div>
    <div class='addmanagerLine'>
      <div class='inputBars'>
        <span>:</span>
         <span style="margin-right:12px"></span>
         <el-select v-model="goutongvalue" placeholder="">
          <el-option
            v-for="item in goutongoptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
         </el-select>
      </div>
      <div class='inputBars'>
        <span>:</span>
         <span style="margin-right:12px"></span>
         <el-select v-model="chengjiaovalue" placeholder="">
          <el-option
            v-for="item in chengjiaooptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
         </el-select>
      </div>
      <div class='inputBars'>
        <span>:</span>
         <span style="margin-right:12px"></span>
        <el-input  class="managerAdd-input" v-model="wechatInput" placeholder="" maxlength=""></el-input>
      </div>
    </div>
    <div class='addmanagerLine'>
        <div class='inputBars'>
        <span style="color:red">*</span><span>:</span>
         <span style="margin-right:12px"></span>
        <el-input  class="managerAdd-input" v-model="managerInput" placeholder="" maxlength=""></el-input>
      </div>
      <el-form :model="numberValidateForm" ref="numberValidateForm" label-width="200px" >
        <el-form-item
            label=":"
            prop="managerphone"
            :rules="[
              { required: true, message: ''},
              { type: 'number', message: ''}
            ]"
  >
    <el-input type="age" v-model.number="numberValidateForm.managerphone" maxlength="11"></el-input>
  </el-form-item>
  </el-form>
    </div>
      <div class="managerImg">
    
      <el-upload
          class="avatar-uploader"
          action="url/smallRoutineCustom/uploadImg"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
          >
          <img v-if="taskPicDetailUrl" :src="taskPicDetailUrl" class="avatar">
          <div v-else class="avatar-uploader-icon">
              <div class="avatar-tipcon">
                  <div class="avatar-text">png jpg</div>
                   <div class="avatar-text">4:3</div>
                  <div class="avatar-text">10M</div>
                  <el-button type="primary" class="el-icon-plus avatar-btn" round></el-button>
              </div>
          </div>
      </el-upload>             
  </div>
  <div class="addMangerbottomBtn">
    <div class="celaddMangerBtn" @click="returnperPage"></div>
    <div class="endaddMangerBtn" @click="commit"></div>
  </div>
</div>
  
  
</template>

<script>
import axios from 'axios'
import url from '../../store/api'
import idCard from '../common/Idcard'
export default {
  name:'addmanager',
  data(){
    return{
      nameInput:'',
      phoneInput:'',
      nicknameInput:'',
      ageInput:'',
      jobInput:'',
      addressInput:'',
      truenameInput:'',
      idcardInput:'',
      wechatInput:'',
      managerInput:'',
      currentInput:'',
      taskPicDetailUrl:'',
      rightTitle:'',
      numberValidateForms:'',
      numberValidateForm:{
        phone:'',
        managerphone:''
      },
        sexoptions: [{
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }],
        sexvalue:'',
        eduvalue:'',
        eduoptions:[{
          value:'',
          label:''
        },{
          value:'',
          label:''
        },{
          value:'',
          label:''
        },{
          value:'',
          label:''
        },{
          value:'',
          label:''
        }],
        marvalue:'',
        maroptions:[{
          value:'',
          label:''
        },{
          value:'',
          label:''
        }],
        goutongoptions:[{
          value:'',
          label:''
        },{
          value:'',
          label:''
        },{
          value:'',
          label:''
        }],
        goutongvalue:'',
        chengjiaovalue:'',
        chengjiaooptions:[{
          value:'',
          label:''
        },{
          value:'',
          label:''
        },{
          value:'',
          label:''
        }]
    }
  },
  created(){
      let iseditManager = sessionStorage.getItem('ToclientEditsession');
      if(iseditManager==='true'){
        this.rightTitle = ''
        this.managerDetail()
      }else{
        this.rightTitle = ''
      }
  },
    mounted(){   
    this.$store.state.defaultActive = '';
    this.$store.state.mainDetailTitle = this.rightTitle;
    this.$store.state.rightTitle = this.rightTitle;
  },
  methods:{
     managerDetail(){
       let managerId = sessionStorage.getItem('clientIdDetail');
       let params = {
        idSCustomInfo : managerId
      }
      this.$store.dispatch("clientDetail", params).then(data => {
      console.log('',data)
        this.nameInput = data.data.customName,
        this.numberValidateForm.phone = data.data.telephone,
        this.nicknameInput = data.data.nickName,
        this.sexvalue = data.data.sex,
        this.ageInput = data.data.age,
        this.eduvalue = data.data.educate,
        this.marvalue = data.data.married,
        this.jobInput = data.data.job,
        this.currentInput = data.data.recentShowAddress,
        this.truenameInput = data.data.identificationName,
        this.addressInput = data.data.address,
        this.idcardInput = data.data.identificationNumber,
        this.goutongvalue = data.data.communicateIntent,
        this.chengjiaovalue = data.data.investIntent,
        this.wechatInput = data.data.wechatNo,
        this.numberValidateForm.managerphone = data.data.customManagerPhone,
        this.managerInput = data.data.customManagerName,
        this.taskPicDetailUrl = data.data.customImg
    });
     },
        beforeAvatarUpload(file) {
        console.log('wenjian',file.type)
        const isJPG = file.type === 'image/jpeg' || file.type === 'image/png';
        const isLt2M = file.size / 1024 / 1024 < 10;
        if (!isJPG) { 
          return  this.$message.error(' JPG/PNG !');
        }
        if (!isLt2M) {
          return this.$message.error(' 10MB!');
        }
      //
            let fd = new FormData();
            fd.append('file', file);
             const loading = this.$loading({
                lock: true,
                text: 'Loading',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)'
             });
             let config = {
               headers:{
                 'csrfToken':'9EA6EC4DCBE5658676919DD8E4686095'
               }
             }
            axios.post(url.clientimg, fd,config).then(res => {
            console.log('' ,res.data)
                    loading.close();
                    if(res.data.code==1)
                    {
                      return this.$message.error(res.data.message)
                    }else{
                       this.taskPicDetailUrl = res.data.data.fullPath
                    }
                   
                    //this.urlinput = res.data.data.fullPath     
             })
             return false;
        // return isJPG && isLt2M;
      },
      handleAvatarSuccess(){
         console.log('success')
      },
    commit(){
      if(this.nameInput == ''){
        return this.$message.error('')
        }
      if(this.numberValidateForm.phone == ''){
        return this.$message.error('')
        }
      if(this.managerInput == ''){
        return this.$message.error('')
      }
      if(this.numberValidateForm.managerphone == ''){
        return this.$message.error('')
      }
      if(this.idcardInput != ""){
        if(idCard(this.idcardInput)==false){
          return this.$message.error('')
        } 
      }
      if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.numberValidateForm.phone))){
        return this.$message.error('')
      }
       if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.numberValidateForm.managerphone))){
        return this.$message.error('')
      }
    let iseditManager = sessionStorage.getItem('ToclientEditsession');
    let managerId = sessionStorage.getItem('clientIdDetail');
      console.log('jieguo',iseditManager)
      if(iseditManager ==='true'){//
      let params = {
          idSCustomInfo:managerId,
          customName:this.nameInput,
          telephone:this.numberValidateForm.phone,
          nickName:this.nicknameInput,
          sex:this.sexvalue,
          age:this.ageInput,
          educate:this.eduvalue,
          married:this.marvalue,
          job:this.jobInput,
          recentShowAddress:this.currentInput,
          identificationName:this.truenameInput,
          address:this.addressInput,
          identificationNumber:this.idcardInput,
          communicateIntent:this.goutongvalue,
          investIntent:this.chengjiaovalue,
          wechatNo:this.wechatInput,
          customManagerPhone:this.numberValidateForm.managerphone,
          customManagerName:this.managerInput,
          customImg:this.taskPicDetailUrl
        }
      this.$store.dispatch("updateClient", params).then(data => {
      console.log('',data.message)
      if(data.code == 1){
        return this.$message.error(data.message)
      }
      this.$router.push('client')
    });

      }else{//
        let params = {
          customName:this.nameInput,
          telephone:this.numberValidateForm.phone,
          nickName:this.nicknameInput,
          sex:this.sexvalue,
          age:this.ageInput,
          educate:this.eduvalue,
          married:this.marvalue,
          job:this.jobInput,
          recentShowAddress:this.currentInput,
          identificationName:this.truenameInput,
          address:this.addressInput,
          identificationNumber:this.idcardInput,
          communicateIntent:this.goutongvalue,
          investIntent:this.chengjiaovalue,
          wechatNo:this.wechatInput,
          customManagerPhone:this.numberValidateForm.managerphone,
          customManagerName:this.managerInput,
          customImg:this.taskPicDetailUrl
        }
      this.$store.dispatch("addClient", params).then(data => {
      console.log('',data)
      if(data.code == 1){
        return this.$message.error(data.message)
      }
      this.$router.push('client')
    });
      }
    },
    returnperPage(){
      this.$router.go(-1)
    }
  }
}

</script>

<style>
.addmanagerLine-containter{
  background: #fff;
}
.addmanagerLine{
  display: flex;
  color: #4A4A4A ;
  font-size: 14px;
  font-family: PingFangSC-Regular;
  justify-content:space-between;
  padding-top: 20px;
  margin-left: 20px;
  margin-right: 20px;
}
.managerAdd-input.el-input{
  width: 200px;
  height: 40px;
}
.addmanagerLine .el-form-item__content{
  width: 200px;
}
.addmanagerLine .el-input{
   width: 200px;
}
.inputBar{
  width: 300px;
  text-align: right;
}
.inputBars{
   width: 320px;
  text-align: right;
  height: 62px;
}
.addmanagerLine .el-input__inner{
  width: 200px;
}
.managerImg{
  display: flex;
  margin-top: 20px;
  margin-left: 60px;
  color: #4A4A4A ;
  font-size: 14px;
  font-family: PingFangSC-Regular;
}
.addMangerbottomBtn{
  display: flex;
  text-align: center;
  justify-content:flex-end;
  margin-right: 20px;
}
.celaddMangerBtn{
  width: 64px;
  height: 40px;
  border: 1px solid #5A9AFB;
  border-radius: 5px;
  line-height: 40px;
  color: #5A9AFB;
  margin-right: 16px;
  cursor: pointer;
}
.endaddMangerBtn{
  width: 64px;
  height: 40px;
  background: linear-gradient(-90deg, #3367FA 0%, #5A9AFB 96%);
  border-radius: 5px;
  color: #fff;
  line-height: 40px;
  cursor: pointer;
}
.avatar-uploader{
    margin-left: 12px;
}
.avatar-uploader .el-upload {
    border: 1px dashed #2F78EC;
    border-radius: 4px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 14px;
    color: #9B9B9B;
    width: 333px;
    height: 220px;
    text-align: center;
    display: flex;
    align-items:center;
    justify-content: center;
  }
  .avatar-uploader-icon:hover{
  background: rgba(0,0,0,0.40);;
  opacity: 0.9;
  color: #fff;
}
.avatar {
    width: 333px;
    height: 220px;
    display: block;
}
</style>



